import DeleteIcon from '@mui/icons-material/Delete'
import { Button, Card, CardActions, CardContent, Divider, IconButton, Stack, Typography } from '@mui/material'
import EditInput from 'components/EditInput'
import { useEffect, useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getWebs } from 'redux/slices/CMS/Web'
import LinkIcon from '@mui/icons-material/Link'
import DialogConfirm from 'components/DialogConfirm'
import LocalFileDisplay from 'components/LocalFileDisplay'
import { IFileLocal, PERMISSION } from 'interfaces'
import { WebApi } from 'API/CMS/web'
import { UIAction } from 'redux/slices/UI'

export default function Page() {
	const dispatch = useAppDispatch()
	const location = useLocation()
	const webs = useAppSelector((s) => s.CMS.Web.list)
	const status = useAppSelector((s) => s.CMS.Web.status)
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const [loading, setLoading] = useState(false)
	const [selectOGImg, setSelectOGImg] = useState(0)
	const [ogFile, setOgFile] = useState<IFileLocal>()
	const [favicon, setFavicon] = useState(0)
	const [faviconFile, setFaviconFile] = useState<IFileLocal>()
	useEffect(() => {
		if (status === 'init') dispatch(getWebs())
	}, [status, dispatch])
	if (!tokenInfo) return null
	return (
		<Stack gap={1}>
			{favicon > 0 && (
				<DialogConfirm
					open={true}
					title=""
					maxWidth="lg"
					primaryButtonColor="success"
					primaryButtonText="設定"
					disabled={!faviconFile}
					loading={loading}
					handleClose={() => {
						setFavicon(0)
					}}
					handleSubmit={async () => {
						if (!faviconFile) return
						try {
							setLoading(true)
							const { error, message } = await WebApi.update({
								id: favicon,
								favicon: faviconFile.name,
							})
							dispatch(
								UIAction.addGlobalMessage({
									message,
									type: error ? 'error' : 'success',
								})
							)
						} finally {
							setLoading(false)
							setFavicon(0)
						}
					}}
				>
					<LocalFileDisplay
						handleSelect={(file) => {
							setFaviconFile(file)
						}}
						select={faviconFile}
						onlyUse="img"
						imgSize={100}
						maxHeight={200}
					/>
				</DialogConfirm>
			)}
			{selectOGImg > 0 && (
				<DialogConfirm
					open={true}
					title=""
					maxWidth="lg"
					primaryButtonColor="success"
					primaryButtonText="設定"
					disabled={!ogFile}
					loading={loading}
					handleClose={() => {
						setSelectOGImg(0)
					}}
					handleSubmit={async () => {
						if (!ogFile) return
						try {
							setLoading(true)
							const { error, message } = await WebApi.update({
								id: selectOGImg,
								ogImage: ogFile.name,
							})
							dispatch(
								UIAction.addGlobalMessage({
									message,
									type: error ? 'error' : 'success',
								})
							)
						} finally {
							setLoading(false)
							setSelectOGImg(0)
						}
					}}
				>
					<LocalFileDisplay
						handleSelect={(file) => {
							setOgFile(file)
						}}
						select={ogFile}
						onlyUse="img"
						imgSize={100}
						maxHeight={200}
					/>
				</DialogConfirm>
			)}
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					Webサイト一覧管理
				</Typography>
				{tokenInfo.permissions[PERMISSION.CMS] > 1 && (
					<Link to="/cms/website/create">
						<Button
							variant="outlined"
							sx={{
								minWidth: 120,
							}}
						>
							新規作成
						</Button>
					</Link>
				)}
			</Stack>
			<Divider />
			<Stack gap={2}>
				{webs.map((web) => {
					return (
						<Card key={web.id} elevation={2}>
							<CardContent>
								<Typography gutterBottom variant="h5" component="div">
									{web.name}
								</Typography>
								<Stack gap={1}>
									<EditInput
										label="URL"
										value={web.ogURL}
										apiEndpoint={'/api/cms/web/' + web.id}
										fieldName="ogURL"
										readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
									/>
									<EditInput
										label="名前"
										value={web.name}
										apiEndpoint={'/api/cms/web/' + web.id}
										fieldName="name"
										readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
									/>
									<Stack direction="row" gap={1} alignItems="center">
										<EditInput label="Favicon" value={web.favicon} readOnly />
										{tokenInfo.permissions[PERMISSION.CMS] > 1 && (
											<IconButton
												onClick={() => {
													setFavicon(web.id)
												}}
											>
												<LinkIcon />
											</IconButton>
										)}
									</Stack>
									<Stack direction="row" gap={1} alignItems="center">
										<EditInput label="OGP画像" value={web.ogImage} readOnly />
										{tokenInfo.permissions[PERMISSION.CMS] > 1 && (
											<IconButton
												onClick={() => {
													setSelectOGImg(web.id)
												}}
											>
												<LinkIcon />
											</IconButton>
										)}
									</Stack>
									<EditInput
										label="説明"
										value={web.ogDescription}
										apiEndpoint={'/api/cms/web/' + web.id}
										fieldName="ogDescription"
										multiline
										rows={4}
										readOnly={tokenInfo.permissions[PERMISSION.CMS] < 2}
									/>
								</Stack>
							</CardContent>
							{tokenInfo.permissions[PERMISSION.CMS] > 1 && (
								<CardActions
									sx={{
										justifyContent: 'space-between',
									}}
								>
									<Link to={`/cms/website/${web.id}/delete`} state={{ location }}>
										<Button color="error" variant="outlined" size="small" endIcon={<DeleteIcon />}>
											ウェブサイト削除
										</Button>
									</Link>
									<Link to={`/cms/${web.id}/page`}>
										<Button color="info" variant="contained">
											ページ一覧管理
										</Button>
									</Link>
								</CardActions>
							)}
						</Card>
					)
				})}
			</Stack>
		</Stack>
	)
}
